@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-contacto {
    @include m.tablet {
        @include m.grid(2, v.$separacion);
        background-image: url(../img/granos_cafe.jpg);
        background-repeat: no-repeat;
        background-position: center right;
        background-size: 40%;
    }
}
.formulario {
    .campo {
        margin-bottom: 2rem;

        &:last-of-type {
            margin-bottom: 0;
        }
        label {
            text-transform: uppercase;
            display: block;
            font-size: 2.2rem;
            font-weight: 700;
        }
        input:not([type="submit"]),
        select {
            width: 100%;
            padding: 1rem;
            border: 5px solid v.$primario;
        }
        input[type="submit"] {
            background-color: v.$primario;
            color: v.$blanco;
            width: 100%;
            border: none;
            padding: 1rem;
            text-transform: uppercase;
            font-size: 2.2rem;
            font-weight: 700;
            &:hover {
                cursor: pointer;
            }
        }
    }
}